#routes {
  position: absolute;
  top: 100vh;
  width: 100%;
}

.route {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
}

.text {
  color: white;
  margin: 20px 160px;
}

.title {
  font-size: 42px;
}

.detail {
  font-size: 24px;
}

.about.route {
  background-color: #579660
}

.code.route {
  background-color: #ad3f60
}

.music.route {
  background-color: #3b76b4
}

.contact.route {
  background-color: #695396;
}

.embed {
  width: 350px;
  height: 350px;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  border: 1px solid white;
}

.embed:hover {
  -webkit-box-shadow: 2px 2px 2px black;
          box-shadow: 2px 2px 2px black;
  -webkit-transition: all .1s ease;
  -o-transition: all .1s ease;
  transition: all .1s ease
}

.info {
  color: white;
  font-size: 24px;
  margin: 10px;
}



